<template>
  <el-container style="height: 100vh; border: 1px solid #eee">
    <el-aside width="auto" style="background-color: rgb(238, 241, 246)">
      <el-menu :router='true' class="el-menu-vertical-demo" default-active="1" :collapse="isCollapse">
        <el-button style="width:100%" class="el-icon-s-fold" @click="isCollapse = false" v-if="isCollapse"></el-button>
        <el-button style="width:100%" class="el-icon-s-unfold" @click="isCollapse = true" v-else></el-button>
        <el-menu-item index='/user'>
          <i class="el-icon-user"></i>
          <span slot="title">用户管理</span>
        </el-menu-item>
        <el-menu-item index='/log'>
          <i class="el-icon-document"></i>
          <span slot="title">打印记录</span>
        </el-menu-item>
        <el-menu-item index='/file'>
          <i class="el-icon-folder-opened"></i>
          <span slot="title">文件管理</span>
        </el-menu-item>
        <el-submenu index='4'>
          <template slot="title">
            <i class="el-icon-message"></i>
            <span slot="title">测试项</span>
          </template>
        </el-submenu>
      </el-menu>
    </el-aside>
    <el-container>
      <!-- <el-header>
        <h1>用户管理</h1>
      </el-header> -->
      <el-main>
        <router-view />
      </el-main>
    </el-container>
  </el-container>
</template>
<script>
export default {
  data() {
    return {
      isCollapse: false,
    }

  },
  async mounted() {
    try {
      const response = await this.$axios.get('/api/vertify', {
        headers: {
          Authorization: localStorage.getItem('token')
        }
      });
      if (response.status === 200) {
        this.$message({
          message: '自动登录成功',
          type: 'success'
        });
        // ({ username: this.username, userRole: this.userRole } = response.data)
      } else {
        // this.$message.error('失败')
      }
    } catch (error) {
      console.log(error);
      // this.$message.error('获取失败')
    }
  }
}
</script>
<style>

h1 {
  text-align: center;
  width: 100%;
  font-size: 30px;
  margin-bottom: 20px;
  margin-top: 10px;
  color: #409EFF;
  font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial, sans-serif;
}

.el-menu-vertical-demo:not(.el-menu--collapse) {
  width: 20vw;
  max-width: 300px;
  min-width: 150px;
}
</style>