<template>
    <div style="width: 100%;max-width: 800px;margin: auto auto;">
        <h1>用户管理</h1>
        <!-- 表格显示用户列表 -->
        <el-table :max-height="viewHeight - 160" :data="users">
            <el-table-column prop="id" label="ID" width="40"></el-table-column>
            <el-table-column prop="username" label="用户名"></el-table-column>
            <el-table-column prop="userRole" label="权限"></el-table-column>
            <el-table-column label="操作" width="155">
                <template slot-scope="scope">
                    <el-button type="primary" @click="editUser(scope.row)">编辑</el-button>
                    <el-popover placement="top-end" width="160" v-model="visible_userDelete[scope.$index]">
                        <p>确定删除吗？</p>
                        <div style="text-align: right; margin: 0">
                            <el-button size="mini" type="text"
                                @click="$set(visible_userDelete, scope.$index, false)">取消</el-button>
                            <el-button type="danger" size="mini"
                                @click="$set(visible_userDelete, scope.$index, false); deleteUser(scope.row)">确定</el-button>
                        </div>
                        <el-button slot="reference" class="el-icon-delete" type="danger"></el-button>
                    </el-popover>
                </template>
            </el-table-column>
        </el-table>
        <!-- 添加用户按钮 -->
        <el-button style="margin: 10px 10px;" type="primary" @click="addDialogVisible = true">添加用户</el-button>

        <!-- 添加用户表单 -->
        <el-dialog :visible.sync="addDialogVisible" title="添加用户" width="70%">
            <el-form :model="userForm" label-width="80px">
                <el-form-item label="用户名">
                    <el-input v-model="userForm.username"></el-input>
                </el-form-item>
                <el-form-item label="权限">
                    <el-select v-model="userForm.userRole" placeholder="选择用户身份">
                        <el-option label="管理员" value="admin"></el-option>
                        <el-option label="普通用户" value="user"></el-option>
                        <el-option label="访客" value="guest" :disabled="true"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="密码">
                    <el-input v-model="userForm.password"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="addDialogVisible = false">取消</el-button>
                <el-button type="primary" @click="addUser">确定</el-button>
            </div>
        </el-dialog>
    </div>
</template>
<script src="https://unpkg.com/element-ui/lib/index.js"></script>
<script>
export default {
    data() {
        return {
            users: [], // 用户列表
            addDialogVisible: false, // 是否显示添加用户对话框
            userForm: {
                username: '',
                password: '',
                userRole: ''

            },
            visible_userDelete: [],
            viewHeight:window.innerHeight
        };
    },
    methods: {
        // 添加用户
        addUser() {
            if (!this.userForm.username) {
                return this.$message.error('请输入用户名');

            }
            if (!this.userForm.userRole) {
                return this.$message.error('请选择用户权限')
            }
            if (!this.userForm.password) {
                return this.$message.error("请输入用户密码")
            }
            // 在这里调用后端 API 将用户添加到数据库
            this.$axios.post('/api/manage/add', this.userForm, {
                headers: {
                    // 自定义请求头
                    'Authorization': localStorage.getItem('token')
                }
            }).then(response => {
                this.$message({
                    message: '添加成功',
                    type: 'success'
                });
                // 然后将用户添加到 users 数组中
                this.users.push(response.data);
                // 重置表单
                Object.keys(this.userForm).forEach(key => {
                    this.userForm[key] = null;
                })
                this.addDialogVisible = false;
            }).catch(error => {
                console.log(error);
                this.$message.error('添加失败');
            })

        },
        // 编辑用户
        editUser(row) {
            // 在这里实现编辑用户的逻辑
            console.log('编辑用户', row);
        },
        // 删除用户
        deleteUser(row) {
            // 在这里调用后端 API 将用户从数据库中删除
            if (row.username == this.username) {
                this.$message.error('无法删除当前登录账号');
                return console.log("无法删除当前登录账号")
            }
            this.$axios.post('/api/manage/delete', { rUsername: row.username }, {
                headers: {
                    // 自定义请求头
                    'Authorization': localStorage.getItem('token')
                }
            }).then(response => {
                // 然后从 users 数组中移除对应的用户
                console.log(response)
                this.users = this.users.filter(user => user.username !== row.username);
                this.$message({
                    message: '删除成功',
                    type: 'success'
                });
            }).catch(error => {
                console.log(error)
                this.$message.error('删除失败');
            })

        },
        getUsers() {
          this.$axios.get('/api/manage/allUsers', {
            headers: {
              // 自定义请求头
              'Authorization': localStorage.getItem('token')
            }
          }).then(response => {
            this.users = response.data;
          }).catch(error => {
            console.log(error)
            this.$message.error('获取失败');
          })
        },
    },
    created() {
        window.addEventListener('resize', () => {
          this.viewHeight = window.innerHeight;
        })
      },
    async mounted() {
        this.getUsers()
    }
}
</script>

<style>
h1 {
    text-align: center;
    color: #409EFF;
    font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial, sans-serif;
}

#app {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    padding: 0 0;
    margin: 0 0;
}
</style>

<link rel="stylesheet" href="https://unpkg.com/element-ui/lib/theme-chalk/index.css">