<template>
    <div>
        <h1>文件管理</h1>
        <!-- 表格显示文件列表 -->
        <el-table :max-height='viewHeight - 130' :data="files">
            <el-table-column prop="timestamp" label="上传时间" sortable></el-table-column>
            <el-table-column prop="filename" label="文件名"></el-table-column>
            <el-table-column prop="username" label="用户名" sortable></el-table-column>
            <el-table-column label="操作" width="155">
                <template slot-scope="scope">
                    <el-button type="primary" @click="downloadFile(scope.row)">下载</el-button>
                    <el-popover placement="top-end" width="160" v-model="visible_fileDelete[scope.$index]">
                        <p>确定删除吗？</p>
                        <div style="text-align: right; margin: 0">
                            <el-button size="mini" type="text"
                                @click="$set(visible_fileDelete, scope.$index, false)">取消</el-button>
                            <el-button type="danger" size="mini"
                                @click="$set(visible_fileDelete, scope.$index, false); deleteFile(scope.row)">确定</el-button>
                        </div>
                        <el-button slot="reference" class="el-icon-delete" type="danger"></el-button>
                    </el-popover>
                </template>
            </el-table-column>
        </el-table>
    </div>
</template>
<script>
export default {
    data() {
        return {
            files: [],
            visible_fileDelete: [],
            viewHeight: window.innerHeight,
        }
    },
    methods: {
        getFiles() {
            this.$axios.get('/api/manage/allFiles', {
                headers: {
                    // 自定义请求头
                    'Authorization': localStorage.getItem('token')
                }
            }).then(response => {
                this.files = response.data;
            }).catch(error => {
                console.log(error)
                this.$message.error('文件列表获取失败');
            })
        },
        downloadFile(row) {
            this.$axios.get('/api/manage/getFile', {
                params: row,
                responseType: 'blob', // 指定响应类型为二进制流
                headers: {
                    // 自定义请求头
                    'Authorization': localStorage.getItem('token')
                }
            }).then(response => {
                const url = window.URL.createObjectURL(response.data);
                const link = document.createElement('a');
                link.href = url;
                link.download = row.filename; // 设置下载文件的文件名
                document.body.appendChild(link);
                link.click();
                window.URL.revokeObjectURL(url);
            }).catch(error => {
                console.log(error)
                console.log(error)
                this.$message.error('下载失败');
            })
        },
        deleteFile(row) {
            console.log(row)
        }
    },
    created() {
        window.addEventListener('resize', () => {
            this.viewHeight = window.innerHeight;
        })
    },
    mounted() {
        this.getFiles();
    }
}
</script>